const SignUp = () => {
    const [id,onChangeId] =useInput('');
    const [password, onChangePassword] = useInput('');
    const [passwordCheck, onChangePasswordCheck] = useInput(''); 
    const [checkTerm, onChangeCheckTerm] = useInput('');
    return(
        <>
            <Form>
                <Input type="text" onChange={onChangeId} value={id}></Input>
                <Input type="password" onChange={onChangePassword} value={password}></Input>
                <Input type="password" onChange={onChangePasswordCheck} value={passwordCheck}></Input>
                <Input type="checkbox" onChange={onChangeCheckTerm}></Input>
            </Form>
        </>
    )
}

export default SignUp;